

import alanBtn from "@alan-ai/alan-sdk-web"

import { useEffect } from "react"
import "../style/home.css"

import { Button, Input } from "reactstrap"
import { useState } from "react"
import { useNavigate } from "react-router-dom"
import { generateToken } from "../firebase-config"
import axios from "axios"
import { toast } from "react-toastify"
import addNotification from "react-push-notification"
import Logo from "../assets/speaker.png"

const App = () => {

    const navigate = useNavigate()

    const [name, setName] = useState('')


    useEffect(() => {

        function updateScreen(time) {

            alanBtn({

                key: "c6eb400ab872978fad3b004399eccbd82e956eca572e1d8b807a3e2338fdd0dc/stage",

                onCommand: (commandData) => {

                    if (commandData.command === "hello") {

                        console.log(commandData.data)
                        setName(commandData.data)
                        navigate("/about")

                    }






                }
            })
        }


        requestAnimationFrame(updateScreen);
    }, [])



    const getFcm = async () => {
        const fcmtoken = await generateToken()


        if (fcmtoken) {
            console.log(fcmtoken)

            axios.post('http://localhost:4000/send_notification', {

                fcmToken: fcmtoken



            })

                .then(d => console.log(d))
                .catch(err => console.log(err))





        }


        else {
            toast.error("Please Allow Notification")
        }
    }


    const get_noti = () => {

        addNotification({
            title: "Humai hai apka khayal 😍",
            message: "abhi join kren aur payen 10% cashback 🕶",
            duration: 4000,
            native: true,
            icon: Logo
        })
    }


    return (
        <div className="home_base">
            HOME
            <Input onClick={() => getFcm()} value={name} style={{ width: "50%" }} placeholder="Your Name"></Input>
            <Button color="success" onClick={() => get_noti()}>Test Notification</Button>
        </div>

    )
}





export default App